.fixed-contact-button{position:fixed;bottom:50px;right:30px;width:80px;height:80px;background-color:rgba(0,0,0,0);color:#007bff;border:1px solid #007bff;border-radius:50%;display:flex;justify-content:center;align-items:center;font-size:1.5rem;cursor:pointer;-webkit-tap-highlight-color:rgba(0,0,0,0);text-decoration:none;transition:box-shadow .3s ease}.fixed-contact-button:hover{box-shadow:0 0 5px #007bff}.fixed-contact-button:hover::before{transform:translateX(-110px);opacity:1}.fixed-contact-button::before{content:"Написать";position:absolute;transform:translateX(0);opacity:0;text-shadow:0 0 2px #007bff;transition:opacity .3s ease,transform .5s ease}.fixed-contact-button .icon-container{position:relative}.fixed-contact-button .icon-container .icon-fixed-button{font-size:1.4em;animation:livited 3s infinite}.fixed-contact-button .icon-container::before{content:"";position:absolute;bottom:0;left:0;width:100%;box-shadow:0 0 15px 4px #007bff;background-color:#007bff;animation:shadowAnime 3s infinite}@keyframes livited{0%,100%{transform:translateY(0)}50%{transform:translateY(-10px)}}@keyframes shadowAnime{0%,100%{transform:scale(0.7)}50%{transform:scale(1.1)}}